@import "~slick-carousel/slick/slick.css"; 
@import "~slick-carousel/slick/slick-theme.css";

.cardsBoatsCs{
    background: #fff;
    padding: 3rem;
}

.cardsBoatsCs .sliderCards{
    margin: 0 auto;
    height: 500px;;
}

.cardsBoatsCs .slick-slide img{
    height: 300px;
    object-fit: cover;
    object-position: center;
    width: 100%;
    margin: 0 auto;
    border-top-right-radius: 25px;
    border-top-left-radius: 25px;
    transition: transform 0.3s;
    transform-origin: center center;
}

.cardsBoatsCs .activeSlide:hover img{
    transform: scale(1.1);
}


.cardsBoatsCs .slide{
    transform: scale(0.4);
    transition: transform 300ms;
    opacity: 0.5;
    text-align: center;
    height: 500px;
    /*background: #061138;*/
    background-image: url('../Assets/card.png');
    border-radius: 25px;
    color: #fff;
    overflow: hidden;
    
}

.cardsBoatsCs .slide > a {
   border-radius: 15px;
}

.cardsBoatsCs .activeSlide{
    transform: scale(0.9);
    opacity: 1;
    text-align: center;
}

.cardsBoatsCs .arrow{
    background-color: #FFF;
    position: absolute;
    cursor: pointer;
    z-index: 10;
}

.cardsBoatsCs .boatTitle{
    margin-top: 30px;
}



.cardsBoatsCs .arrow svg{
    transition: color 300ms;
}

.cardsBoatsCs .arrow svg:hover{
    color: #68edff;
}

.cardsBoatsCs .next{
    right: 0;
    top: 50%;
}

.cardsBoatsCs .prev{
    left: 0;
    top: 50%;
}


@media screen and (max-width:991px) {
    .cardsBoatsCs .slide > a {
        width: 180px;
    }

    .title__Section{
        font-size: 30px;
    }

    .subtitle__Section{
        font-size: 18px;
    }

    .boatTitle{
        font-size: 16px;
        font-weight: bold;
    }

    .boat__location{
        font-size: 15px;
        font-style: italic;
    }

    .boat__price{
        font-size: 14px;
    }
}

@media screen and (max-width:425px) {
    .title__Section{
        font-size: 30px;
    }

    .cardsBoatsCs{
        padding: 1rem;
    }
}